import React from 'react'
import web_address from "./WebAddress"
export default class AddPostForm extends React.Component {
  constructor(){
    super();
    this.state = {
      title: "",
      elements: []
    }
    this.add_element = this.add_element.bind(this);
    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.commitHandler = this.commitHandler.bind(this);
  }

  add_element(e){
    var name = e.target.name;
    if (name === "paragraph"){
      console.log("ADDING PARAGRAPH")
    this.setState((prevState) =>
    {
      let new_elements = prevState.elements;
      new_elements.push("DEMO " + new_elements.length)
      return {
        elements: new_elements
      }
    })
  }
  else if (name === "image"){
    console.log("ADDING IMAGE")
    this.setState((prevState) =>
    {
      let new_elements = prevState.elements;
      new_elements.push(
        {src: "", alt: ""}
      )
      return {
        elements: new_elements
      }
    })
  }
  }

  onChangeHandler(e){
    var {name, value} = e.target;
    console.log(this.state)
    if(name.includes("src") || name.includes("alt")){
      this.setState((prevState) => {
        let elements  = prevState.elements
        let n = parseInt(name.replace("alt-", "").replace("src-", ""));
        var mod = elements[n];
        if (name.includes("src")){
          mod.src =  value
        }else if (name.includes("alt")){
          mod.alt =  value
        }
        elements[n] = mod;
        return (
          {
            elements: elements}
        );
      })
    } else  if (name.includes("paragraph")){
      this.setState((prevState) => {
        let elements = prevState.elements
        let n = parseInt(name.replace("paragraph-", ""));
        console.log(elements)
        elements[n] = value;
        return (
          {
            elements: elements}
        );
      })
    }
    else {
      this.setState({
        [name]: value
      })
    }
  }

  commitHandler(e){
    e.preventDefault();
    console.log(this.state.elements);
    let url = web_address + "/api/" + this.props.username + "/" + this.props.password + "/add_entry/" + this.state.title;
    console.log(url)
    fetch(url, {
    method: 'POST', // or 'PUT'
    body: JSON.stringify(this.state.elements), // data can be `string` or {object}!
    headers:{
      'Content-Type': 'application/json'
    }
  }).then(res => console.log(res.status))

  }

  render(){
    return (
      <div id="add-post-form">
      <input
        type="text"
        id="title"
        key="title"
        name="title"
        value={this.state.title}
        onChange={this.onChangeHandler}
        placeholder="Title"
      />
        {this.state.elements.map((elem) => {
          let n = this.state.elements.indexOf(elem)
          console.log(elem.src)
          if (elem.src !== undefined | elem.alt !== undefined){
            return (
                <div id="choose-image" key={n}>
                  <form>
                    <input
                      type="text"
                      id="src"
                      key={"src-" + n}
                      name={"src-" + n}
                      value={this.state.elements[n].src}
                      onChange={this.onChangeHandler}
                      placeholder="Source"
                    />
                    <br />
                    <input
                      type="text"
                      id="alt"
                      key={"alt-" + n}
                      name={"alt-" + n}
                      value={this.state.elements[n].alt}
                      onChange={this.onChangeHandler}
                      placeholder="Alternative"
                    />
                  </form>
                </div>
            )
          }
          else{
          return (
            <textarea
              rows="5"
              cols="50"
              name={"paragraph-" + n}
              key={"paragraph-" + n}
              onChange={this.onChangeHandler}
              value={this.state.elements[n]}
            />);
        }
        }
      )
        }
        <button name="paragraph" onClick={this.add_element}>Add Paragraph</button>
        <button name="image" onClick={this.add_element}>Add Image</button>
        <button name="commit" onClick={this.commitHandler}>Commit Changes</button>
      </div>
  );
}
}
