import React from 'react'
import LoginDialogue from "./LoginDialogue"
import web_address from "./WebAddress"
import "./App.css"
import md5 from 'md5';
import ControlPanel from './ControlPanel'
class App extends React.Component {
  constructor(){
    super();
    this.state = {
      username: "",
      password: "",
      canAuthenticate: false,
      message: null
    }
    this.onChangeHandler = this.onChangeHandler.bind(this);
    this.signInHandler = this.signInHandler.bind(this);
  }

  onChangeHandler(e){
    let {name, value} = e.target;
    console.log(name, value)
    this.setState({
      [name]: value
    })
  }

  signInHandler(e){
    e.preventDefault()

    console.log(md5(this.state.username), md5(this.state.password))
    fetch(web_address + "/api/" + md5(this.state.username) + "/" + md5(this.state.password) + "/authenticate")
    .then((response) => {
          console.log(response)
      if(response.status === 200){
        this.setState((prevState) => {
          return ({
          canAuthenticate: true,
          usernamemd5:  md5(prevState.username),
          passwordmd5:  md5(prevState.password)
        });
        })
      }
      else {
        this.setState({
          message: "Wrong Username or Password"
        })
      }
      }
    )
  }



  render(){
  return (
    <div id="admin-panel">

    {this.state.canAuthenticate ?
      <ControlPanel username={this.state.usernamemd5} password={this.state.passwordmd5}/>
      :
    <LoginDialogue
    username={this.state.username}
    password={this.state.password}
    onChangeHandler={this.onChangeHandler}
    signInHandler={this.signInHandler}
    message={this.state.message}/>
    }
    </div>
  );
}
}
export default App;
