import React from 'react'
import './LoginDialogue.css'
class LoginDialogue extends React.Component {
  render(){
  return (
    <div id="login">
      <form>
        <input type="text" id="username" name="username" value={this.props.username} onChange={this.props.onChangeHandler} placeholder="Username"/>
        <br />
        <input type="text" id="password" name="password" value={this.props.password} onChange={this.props.onChangeHandler} placeholder="Password"/>
        <br />
        <button onClick={this.props.signInHandler}>Sign In</button>
      </form>
      {this.props.message ? <h3>{this.props.message}</h3> : null}
    </div>
  );
}
}
export default LoginDialogue
