import React from 'react'
import web_address from "./WebAddress"
export default class CommitButton extends React.Component {
  constructor(){
    super();
    this.onClickHandler = this.onClickHandler.bind(this)

  }

  onClickHandler(e){
    e.preventDefault();
    fetch(web_address + "/api/" + this.props.username + "/" + this.props.password + "/update_blog");
  }

  render(){
    return (
      <button onClick={this.onClickHandler}>Publish</button>
    );
  }

}
